body {
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  display: flex;
}

.stopwatch {
  border: 1vmin dotted #000;
  border-radius: 100%;
  justify-content: center;
  align-items: center;
  width: 80vmin;
  height: 80vmin;
  display: flex;
  position: relative;
}

.stopwatch__core {
  z-index: 1;
  background-color: #f6a603;
  border-radius: 100%;
  width: 5vmin;
  height: 5vmin;
}

.stopwatch__seconds-hand {
  transform-origin: bottom;
  background-color: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  animation: 60s linear infinite seconds;
  position: absolute;
}

.stopwatch__minutes-hand {
  transform-origin: bottom;
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  animation: 3600s linear infinite minutes;
  position: absolute;
}

.stopwatch--speed-up .stopwatch__seconds-hand {
  animation: 10s linear infinite seconds;
}

.stopwatch--speed-up .stopwatch__minutes-hand {
  animation: 600s linear infinite minutes;
}

@keyframes seconds {
  from {
    transform: translateY(-50%)rotate(0);
  }

  to {
    transform: translateY(-50%)rotate(360deg);
  }
}

@keyframes minutes {
  from {
    transform: translateY(-50%)rotate(0);
  }

  to {
    transform: translateY(-50%)rotate(360deg);
  }
}
